/* ==========================================================================
   #HEADER
   ========================================================================== */




$header-max-width: $inuit-wrapper-width !default;

$header-tool-font-size: $global-smaller-type !default;
$header-topnav-font-size: $global-small-type !default;
$header-topnav-font-size-mobile: $global-tiny-type !default;

$header-searchbar-height: 50px;
$header-searchbar-height-mobile: 36px;

.c-header {
  @include inuit-clearfix();

  @include mq($from: desktop) {
    /* padding: $unit; */
    padding: 0;
    line-height: 1;
    background-color: inherit;
  }
}

/*
.c-header__banner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  margin: $unit auto;
}
*/


// Top navigation bar

.c-header__topnav {
  background-color: $global-darkblue;
}

.c-header__topnav-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;

  margin: 0 auto;
  max-width: $header-max-width;

  list-style: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.c-header__topnav-item {
  display: inline-block;
  @include font-size($header-topnav-font-size-mobile, 1);
  color: $global-white;
  padding: $unit/4;

  transition: $global-opacity-transition;
  -webkit-transition: $global-opacity-transition;
  -moz-transition: $global-opacity-transition;
  -ms-transition: $global-opacity-transition;
  -o-transition: $global-opacity-transition;

  &:hover {
    opacity: .65;
  }

  @include mq($from: desktop) {
    @include font-size($header-topnav-font-size);
    padding: $unit/3;
  }
}


// Main navigation

.c-header__nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  @include mq($until: desktop) {
    padding: $unit;
    flex-wrap: wrap;
  }

  @include mq($from: desktop) {
    overflow: hidden;
    max-width: $header-max-width;
    margin: $unit*2 auto;
  }
}

.c-header__nav-bars {
  text-align: left;
}

.c-header__nav-logo {
  padding-left: 0;
  /*fix the redundant padding from o-layout__item*/

  >[href="#"] {
    line-height: 0;
    display: block;
  }

  @include mq($until: desktop) {
    text-align: center;
  }
}

.c-header__nav-logo-img {
  width: $unit;

  @include mq($from: desktop) {
    width: $unit*1.5;
  }
}

.c-header__nav-search {
  [type="search"] {
    height: $header-searchbar-height;

    @include mq($until: desktop) {
      height: $header-searchbar-height-mobile;
    }
  }
}

.c-header__nav-tool {
  @include font-size($header-tool-font-size);
  text-align: right;

  [class^="fa"],
  .svg-inline--fa {
    margin-right: $unit/3;

    @include mq($until: desktop) {
      margin-right: 0;
    }
  }

  [data-display="inline-flex"] {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: end;
    -ms-flex-align: end;
    align-items: flex-end;
  }

  >* {
    margin-right: $unit;

    &:last-child {
      margin-right: 0;
    }
  }
}

.c-header__cart {
  >[class^="fa"] {
    position: relative;
  }
}

.c-header__cart-counter {
  @include font-size(8px);
  color: $global-darkblue;

  position: absolute;
  top: 0;
  right: 50%;
  padding: 0px 8px;

  background-color: $global-white;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.16),
    0 1px 2px #dfe1e5;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.16),
    0 1px 2px #dfe1e5;
}

// Category list
[data-section="Category List"][data-section="Category List"] {
  background-color: $global-darkblue;
  max-width: $global-10-col-width;
  margin: 0 auto;
}